#wpadminbar {
	#wp-admin-bar-jeg-kit {
		.jeg-kit-pro {
			display: inline-flex;
			gap: 6px;
		}

		#wp-admin-bar-jeg-kit-default {
			padding-bottom: unset;

			& > li:last-child:not(#wp-admin-bar-jeg-kit-pro) {
				padding-bottom: 9px;
			}

			#wp-admin-bar-jeg-kit-menu-border {
				.ab-item {
					margin-top: 9px;
					height: 1px;
					background: #ffffff1a;
				}
			}

			#wp-admin-bar-jeg-kit-rate-us {
				.star-icon {
					display: inline-flex;
					gap: 6px;
					margin-left: 3px;
				}
			}

			#wp-admin-bar-jeg-kit-pro {
				margin-top: 9px;
				padding-bottom: 6px;
				background-color: #faa71c;

				&:hover {
					background-color: #e5941f;
				}

				.ab-item {
					background: unset;
					color: white;
				}
			}
		}

		&:hover, &.hover {
			> .ab-item .jeg-kit-pro svg path {
				fill: currentColor;
			}
		}
	}
}

#toplevel_page_jkit {
	a:hover {
		.wp-menu-image {
			img {
				filter: brightness(0) saturate(100%) invert(74%) sepia(13%) saturate(1685%) hue-rotate(182deg) brightness(98%)
					contrast(93%);
			}
		}
	}

	.wp-menu-image {
		img {
			opacity: 1;
			padding: 7px 0 0;
		}
	}

	&.wp-has-current-submenu {
		&.wp-menu-open {
			.wp-menu-image {
				img {
					filter: brightness(0) saturate(100%) invert(100%);
				}
			}
		}
	}

	.wp-submenu {
		li:last-child {
			a[href*="jegkit.com"] {
				margin: 0px 5px;
				padding: 8px 14px;
				display: flex;
				gap: 5px;
				justify-content: center;
				align-items: center;
				background: linear-gradient(180deg, #ffb12d 0, #f59e0b 100%);
				color: white;
				border-radius: 5px;
				transition: all 0.3s ease;

				&:hover,
				&:focus {
					box-shadow: unset;
					transform: scale(0.94);
					background: linear-gradient(180deg, #f1a627 0%, #e89812 100%);
				}
			}
		}
	}
}
